* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

ul,
li,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: inherit;
}

header {
    text-align: center;
    margin: 50px 0 100px 0;
}

.list {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 60px 20px;
    max-width: 1400px;
    margin: 0 auto;

}

.list img {
    vertical-align: middle;
    max-width: 100%;
    width: 100%;
    height: 200px;
    object-fit: cover;
}

footer {
    text-align: center;
}